<style>
  .echarts {
    width: 100%;
    height: 100%;
  }
</style>

<template>
  <chart  :options="polar"/>
</template>

<script>
  import 'echarts/lib/chart/bar'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/polar'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/title.js'
  import 'echarts/lib/chart/pie'


  export default {
    name:"HelloWorld",
    data () {
      return {
        polar: {
          title : {
            text: '雅居乐花园小区"四返"隔离人员统计',
            subtext: '动态数据',
            x:'center'
          },
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            show: true,
            orient: 'vertical',
            left: 'left',
            data: ['两省四市','湖北武汉','网格化管理','其他四返人员']
          },
          series : [
            {
              name: '隔离人员数量',
              type: 'pie',
              radius : '55%',
              center: ['50%', '60%'],
              data:[
                {value:335, name:'两省四市'},
                {value:310, name:'湖北武汉'},
                {value:234, name:'网格化管理'},
                {value:135, name:'其他四返人员'}
              ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        }
      }
    }
  }
</script>



